import React, {useRef } from 'react';

import useIsVisible from '../Hooks/UseIsVisible';


const WorkItem = (workItem) =>{
    const workImage = 'images/icons/'+workItem.image;
    const ref = useRef();
    const isVisible = useIsVisible(ref);

    return <div className='perspective'>
        <div ref={ref} className={'workItem ' + (isVisible ? 'anim' :'')}>
            <h3>{workItem.company}</h3>
            <p className="info">
            <img src={workImage} className='logo'/>
                {workItem.title}<span>&bull;</span> <em className="date">{workItem.years}</em></p>
            <ul className='workItems'>
                {workItem.description.map((item, i) => <li key={i}>
                {item}
            </li>)}
            </ul>
        </div>
    </div>
}

export default WorkItem;



